import React, {useEffect} from 'react';
import {connect} from "react-redux";
import {getActiveTeamList} from "../../redux/action/CreateTeamAction";
import {getAllAgents} from "../../redux/action/createAgentAction";

const AgentsTable = (props) => {

    useEffect( ()=>{



        props.getAllAgents()
    }, [])


    return (
        <div className='agents-table'>
           <table>
               <thead>

               <tr>
                   <th></th>
                   <th>
                       <button><img src="./img/icon/arrowUpDown.png" alt=""/>Name</button>
                   </th>
                   <th>
                       <button><img src="./img/icon/arrowUpDown.png" alt=""/>Role</button>
                   </th>
               </tr>
               </thead>

               <tbody>






               {
                   props.getAllAgentsList ?  props.getAllAgentsList.map(item =>(
                       <tr>

                           <td>
                               <div className="agents-table-body-account">
                                   <img src={item.image ? item.image : "img/icon/account3.png" } alt=""/>
                               </div>
                           </td>
                           <td>
                               <div className="agents-table-body-name">
                                   <h3>{item.full_name}</h3>
                                   <p>{item.email}</p>
                               </div>
                           </td>


                           <td>
                               <div className="agents-table-body-role">
                                   <span className="agents-role-admin">{item.role}</span>
                               </div>
                           </td>
                       </tr>
                   ))
                       :

                       ''
               }




               </tbody>


           </table>
        </div>
    );
};


const mapStateToProps =(state)=>{

    return{
        getAllAgentsList: state.createAgentData.getAllAgentsList,

    }
}

export default connect(mapStateToProps, {getAllAgents })(AgentsTable);