import React, {useEffect} from 'react';
import {Link, NavLink} from "react-router-dom";
import {connect} from "react-redux";
import {filterStatusFunction, getAllTicket, getStatusList} from "../redux/action/TicketsAction";


const SideBarMenus = (props) => {

    useEffect(()=>{
        props.getStatusList()
    }, [])


    const filterByStatus = (data) =>{

        props.filterStatusFunction(data)

    }


    const getAllStatusList =()=>{
        props.getAllTicket()
    }
    return (

        <div className="side-bar-menus">
            <div className="side-bar-menus-child">

                <h1 className="open-sans-extra-bold">Tickets</h1>


                <Link to='/tickets/new-ticket' className="new-ticket open-sans-extra-bold">


                    <img src="/img/icon/plus.png" alt="plus icon"/>
                    New ticket
                </Link>

                <div className="ticket-search">
                    <img src="/img/icon/loupe.png" alt=""/>
                    <input type="text" placeholder="Search..."  />

                </div>

                <div className="menus">
                    <Link to="/tickets/all-tickets">
                        <button  onClick={()=>getAllStatusList()} ><span>All tickets</span></button>

                    </Link>

                    <a><span>Tickets to handle</span><span>32</span></a>
                    <a><span>My open tickets</span><span>32</span></a>



                    <h4>My views</h4>
                    <a><span> My tickets in last 7 days</span><span>32</span></a>




                    <h4>Statuses</h4>


                    {
                        props.ticketStatusList.map(item=>(
                            <button onClick={()=>filterByStatus(item.name)} ><span>{item.name}</span><span>32</span></button>

                        ))
                    }

                </div>
            </div>

        </div>
    );
};


const mapStateToProps =(state)=>{
    return{
        ticketStatusList: state.ticketData.ticketStatusList,

    }
}
export default connect(mapStateToProps, {getStatusList,getAllTicket, filterStatusFunction})(SideBarMenus);