                            {/*        <AvRadio customInput value="admin"/>*/}
                            {/*        <label>*/}
                            {/*            <h4>Admin</h4>*/}
                            {/*            <p>*/}
                            {/*                Full access to global settings, adding new agents, subscription and all*/}
                            {/*                actions related to tickets.*/}
                            {/*            </p>*/}
                            {/*        </label>*/}

                            {/*    </div>*/}


                            {/*    <div className="d-flex">*/}

                            {/*        <AvRadio customInput value="agent" />*/}
                            {/*        <label>*/}
                            {/*            <h4>Agent</h4>*/}
                            {/*            <p>*/}
                            {/*                Access to personal settings and all actions related to tickets.*/}
                            {/*            </p>*/}
                            {/*        </label>*/}
                            {/*    </div>*/}


                            {/*    <div className="d-flex">*/}

                            {/*        <AvRadio customInput value="viewer"  className="b-0"/>*/}
                            {/*        <label>*/}
                            {/*            <h4>Viewer</h4>*/}
                            {/*            <p>*/}
                            {/*                Viewers can read and browse tickets but can’t take any actions.*/}
                            {/*            </p>*/}
                            {/*        </label>*/}

                            {/*    </div>*/}


                            {/*</AvRadioGroup>*/}
