const mapStateToProps =(state)=>{
    return {

        getAccount: state.createAgentData.getAccount,
        activeAgentslList: state.createTeamData.activeAgentslList,

    }
}

 export default connect(mapStateToProps, {getInfoAccount,saveFile, updateUserInfo,getActiveTeamList, changePasswordForSend})(InfoAccount) ;