import react, {Component} from 'react'
import ReactApexChart from 'react-apexcharts'

class PieChart extends  Component {
    constructor(props) {
        super(props);

        this.state = {

            series: [44, 55, 13, 33, 23, 13],
            options: {
                chart: {
                    width: 380,
                    type: 'donut',
                },
                dataLabels: {
                    enabled: false
                },
                responsive: [{
                    breakpoint: 480,
                    options: {
                        chart: {
                            width: 200
                        },
                        legend: {
                            show: false
                        }
                    }
                }],
                legend: {
                    position: 'right',
                    offsetY: 0,
                    height: 230,
                }
            },


        };
    }





    render() {
        return (


            <div>
                <div class="chart-wrap">
                    <div id="chart">
                        <h3>Tickets</h3>
                        <ReactApexChart options={this.state.options} series={this.state.series} type="donut" width={380} />
                    </div>
                </div>

            </div>
        );
    }
}


 export default PieChart