import {UPDATE_STATE} from "../actionTypes/createAgentType";


const initialState ={

    getTickets: [],
    ticketStatusList: [],
    commitList: [],
    ticketInfoObject: {}

}
export const TicketsReducer = (state= initialState, action) => {
    switch (action.type){
        case UPDATE_STATE:
            return {...state, ...action.payload}
        default: return state;
    }
}
