import React, {useEffect} from 'react';
import {ticketInfo, updateState} from "../../redux/action/TicketsAction";
import {connect} from "react-redux";
import axios from "axios";
import {API_PATH, TOKEN_NAME} from "../../tools/constants";

const TicketInfoRightPair = (props) => {

    useEffect(()=>{
        props.ticketInfo(window.location.pathname.slice(16))



    }, [])


    return (
        <div className="ticket-info-right-pair">


            <div className="ticket-info-right-pair-header">
                <h2>Details</h2>

                <button>
                    <img src="/img/icon/bin.png"  />

                    Delete
                </button>
            </div>
            <div className="ticket-info-right-pair-body">

                <h2>Ticket info</h2>

                <div className="row">
                    <div className="col-6">
                        <h5>
                            Ticket ID:
                        </h5>
                    </div>
                    <div className="col-6">
                        <h6>ticketid/
                            {
                                props.ticketInfoObject.id
                            }
                        </h6>
                    </div>
                    <div className="col-6">
                        <h5>
                            Created:
                        </h5>
                    </div>
                    <div className="col-6">
                        <h6>
                            {
                                props.ticketInfoObject.date_created?.slice(0,10)
                            }
                        </h6>
                    </div>
                    <div className="col-6">
                        <h5>
                            Last message:
                        </h5>
                    </div>
                    <div className="col-6">
                        <h6>
                            {
                                props.ticketInfoObject.date_modified?.slice(0,10)
                            }
                        </h6>
                    </div>
                    <div className="col-6">
                        <h5>
                            Status:
                        </h5>
                    </div>
                    <div className="col-6">
                        <h6>
                            {
                                props.ticketInfoObject.ticket_status_name
                            }
                        </h6>
                    </div>
                    <div className="col-6">
                        <h5>
                            Rating:
                        </h5>
                    </div>
                    <div className="col-6">
                        <h6>
                            Not rated
                        </h6>
                    </div>
                    <div className="col-6">
                        <h5>
                            Priority:
                        </h5>
                    </div>
                    <div className="col-6">
                        <h6>
                            {
                                props.ticketInfoObject.from_where
                            }
                        </h6>
                    </div>
                    <div className="col-6">
                        <h5>
                            Source:
                        </h5>
                    </div>
                    <div className="col-6">
                        <h6>
                            Created manually
                        </h6>
                    </div>
                </div>


                <img src="/img/Border.png" className="w-100 mt-3"  alt=""/>




                <div className="ticket-responsibility mt-3">
                    <h2>Responsibility</h2>


                    <div className="row">
                        <div className="col-12 d-flex justify-content-between">
                            <h3>Team</h3>
                            <span className="change">Change</span>
                        </div>

                        <div className="ticket-team-info col-12  ">
                            <img src="/img/icon/account3.png" className="mr-3" alt=""/>
                            <span className="team-name">{props.ticketInfoObject.team_name}</span>
                        </div>
                    </div>







                    <div className="row">
                        <div className="col-12 d-flex justify-content-between">
                            <h3>Agent</h3>
                            <span className="change">Change</span>
                        </div>

                        <div className="ticket-team-info col-12  ">
                            <img src="/img/icon/account3.png" className="mr-3" alt=""/>
                            <span className="team-name">{props.ticketInfoObject.sender_name}</span>
                        </div>
                    </div>


                    <div className="row">
                        <div className="col-12 d-flex justify-content-between">
                            <h3>Requester</h3>
                            <span className="change">Change</span>
                        </div>

                        <div className="ticket-team-info col-12  ">
                            <img src="/img/icon/account3.png" className="mr-3" alt=""/>
                            <span className="team-name">{props.ticketInfoObject.receiver_name}</span>
                        </div>
                    </div>

                </div>
            </div>

            </div>
    );
};

const mapStateToProps = (state) => {
  return{
      ticketInfoObject: state.ticketData.ticketInfoObject,

  }
}
export default connect(mapStateToProps, {ticketInfo})(TicketInfoRightPair) ;